    --color-bg: var(--color-concrete-white);
    --color-text: var(--color-charcoal-blue);
    --color-secondary: var(--color-steel-gray);
    --color-accent: var(--color-everbuild-orange);
    --color-link: var(--color-blueprint-teal);
    --color-border: #E0E0E0;

    /* Additional Semantic Colors */
