/* ============================================
   Custom Animations
   ============================================ */

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-fade-in {
  animation: fadeIn 0.6s ease-out;
}

.animate-slide-in-left {
  animation: slideInLeft 0.6s ease-out;
}

.animate-slide-in-right {
  animation: slideInRight 0.6s ease-out;
}

/* ============================================
   Scroll Animations
   ============================================ */

.scroll-reveal {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.scroll-reveal.visible {
  opacity: 1;
  transform: translateY(0);
}

/* ============================================
   React Calendar Timeline Custom Styles
   ============================================ */

/* Current Period Marker - Subtle highlighting */
.react-calendar-timeline .rct-vertical-lines .rct-vl.current-period-marker {
  background-color: rgba(26, 188, 156, 0.08) !important;
  border: none !important;
  width: 100% !important;
  z-index: 1 !important;
}

/* Override library defaults with EverBuild design system */
.react-calendar-timeline {
  font-family: inherit !important;
}

.react-calendar-timeline .rct-sidebar {
  background-color: #F9FAFB !important;
  border-right: 1px solid #E5E7EB !important;
  position: relative !important;
}

/* Sidebar resize handle */
.timeline-resize-handle {
  position: absolute;
  top: 0;
  right: -4px;
  bottom: 0;
  width: 8px;
  cursor: col-resize;
  z-index: 100;
  background: transparent;
  transition: background-color 0.2s ease;
}

.timeline-resize-handle:hover {
  background-color: rgba(26, 188, 156, 0.3);
}

.timeline-resize-handle::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 3px;
  height: 40px;
  background-color: rgba(26, 188, 156, 0);
  border-radius: 2px;
  transition: background-color 0.2s ease;
}

.timeline-resize-handle:hover::after {
  background-color: var(--color-blueprint-teal);
}

.react-calendar-timeline .rct-sidebar-row {
  padding: 0.75rem 1rem !important;
  border-bottom: 1px solid #E5E7EB !important;
  font-size: 0.875rem !important;
  color: var(--color-charcoal-blue) !important;
}

.react-calendar-timeline .rct-header-root {
  background-color: #F9FAFB !important;
  border-bottom: 2px solid #E5E7EB !important;
}

.react-calendar-timeline .rct-calendar-header {
  border-left: 1px solid #E5E7EB !important;
  border-bottom: 1px solid #E5E7EB !important;
  color: var(--color-charcoal-blue) !important;
  font-weight: 500 !important;
  font-size: 0.875rem !important;
}

.react-calendar-timeline .rct-dateHeader {
  border-left: 1px solid #E5E7EB !important;
  color: var(--color-steel-gray) !important;
  font-size: 0.75rem !important;
}

/* Timeline items (phase and task bars) */
.react-calendar-timeline .rct-item {
  border: none !important;
  border-radius: 0.375rem !important;
  cursor: grab !important;
  /* Only transition visual effects, NOT colors/opacity to prevent flashing during re-renders */
  transition: transform 0.2s ease, box-shadow 0.2s ease !important;
}

.react-calendar-timeline .rct-item:active {
  cursor: grabbing !important;
}

.react-calendar-timeline .rct-item:hover {
  transform: scale(1.02) !important;
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1) !important;
  z-index: 10 !important;
}

/* Phase bars - colors are now handled by custom renderer */
.react-calendar-timeline .rct-item.timeline-phase {
  color: white !important;
  font-weight: 600 !important;
  font-size: 0.875rem !important;
}

/* Task bars - colors are now handled by custom renderer */
.react-calendar-timeline .rct-item.timeline-task {
  color: white !important;
  font-weight: 500 !important;
  font-size: 0.75rem !important;
}

/* Today indicator line (will be added in Phase 2) */
.react-calendar-timeline .rct-today {
  background-color: var(--color-blueprint-teal) !important;
  opacity: 0.3 !important;
  width: 2px !important;
  z-index: 20 !important;
}

/* Vertical grid lines */
.react-calendar-timeline .rct-vl {
  border-left: 1px solid #F3F4F6 !important;
  z-index: 0 !important;
}

/* Horizontal grid lines */
.react-calendar-timeline .rct-hl {
  border-bottom: 1px solid #F3F4F6 !important;
  z-index: 0 !important;
}

/* Current time indicator */
.react-calendar-timeline .rct-cursor-line {
  background-color: var(--color-warning-amber) !important;
  width: 2px !important;
}

/* Item content padding */
.react-calendar-timeline .rct-item-content {
  padding: 0.5rem 0.75rem !important;
  white-space: nowrap !important;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
}
